.App {
    padding: 30px;
    display: flex;
}

.App > div {
    margin-right: 50px;
}

input:focus {
    outline: none;
}

.error {
    border: red 1px solid;
}

.error-message {
    color: red;
}

.active-filter {
    background-color: aquamarine;
}

.is-done {
    opacity: 0.5;
    text-decoration: line-through;
}
